// import Graph from './Graph.js';
import React from "react";
import DataMbti from "../data/Mbti.json";
import {useState} from "react";

export default function Mbti(){
    let arr = []; 
    let [stat,setData] = useState(DataMbti.MBTI);

    let reText='';
    
    function m_Click(e){
        const elSpan = document.querySelector('.result span');
        let eTarget = e.target.title
        reText += eTarget;
        // reText += e.target.title;
        elSpan.textContent = reText;
    }

    function okClick(){
        const elH3 = document.querySelector('.reMbti h3');
        const elReSpan = document.querySelector('.reSpan span');
        const elReP = document.querySelector('.reP p');
        DataMbti.MBTI.forEach(function(stat,k){
            // console.log(stat['name'])
            if(reText == stat['name']){
                elH3.textContent = reText;
                elReSpan.textContent = stat['tit'];
                elReP.textContent = stat['description'];
            }
        })
        if(reText == ''){
            alert('성격 유형을 입력해 주세요.')
        }
        
    }

    function reset(){
        const elSpan = document.querySelector('.result span');
        const elH3 = document.querySelector('.reMbti h3');
        const elReSpan = document.querySelector('.reSpan span');
        const elReP = document.querySelector('.reP p');
        reText = '';
        elSpan.textContent = '';
        elH3.textContent = '';
        elReSpan.textContent = '';
        elReP.textContent = '';
    }
    

    return(
        <>
            <section className="mbti">
                <div className ="mTest">
                    <article>
                        <h3>5초 컷 초간단 MBTI TEST</h3>
                        <p>문제가 생겼을 때<br/>당신의 대처 법은?</p>
                    </article>
                    <div className="mClick">
{/* -----------------------------------------------------------------------------> */}
                        <div className="m1">
                            <a className="btn" value="E" onClick={m_Click}>
                                <code className="left" title="E">E</code>
                                <p title="E">말이 많아짐</p>
                            </a>
                            <span>VS</span>
                            <a className="btn" title="I" onClick={m_Click}>
                                <p title="I">생각이 많아짐</p>
                                <code title="I" className="right">I</code>
                            </a>
                        </div>
{/* -----------------------------------------------------------------------------> */}
                        <div className="m1">
                            <a className="btn" title="S" onClick={m_Click}>
                                <code className="left" title="S">S</code>
                                <p title="S">그냥 그런가보다</p>
                            </a>
                            <span>VS</span>
                            <a className="btn" title="N" onClick={m_Click}>
                                <p title="N">어떻게<br/>그럴 수 있지</p>
                                <code title="N" className="right">N</code>
                            </a>
                        </div>
{/* -----------------------------------------------------------------------------> */}
                        <div className="m1">
                            <a className="btn" title="F" onClick={m_Click}>
                                <code title="F" className="left">F</code>
                                <p title="F">이해가 안되는데<br/>공감은 감</p>
                            </a>
                            <span>VS</span>
                            <a className="btn" title="T" onClick={m_Click}>
                                <p title="T">이해가 되야<br/>공감을 하든 말든</p>
                                <code title="T" className="right">T</code>
                            </a>
                        </div>
{/* -----------------------------------------------------------------------------> */}
                        <div className="m1">
                            <a className="btn" title="J" onClick={m_Click}>
                                <code title="J" className="left">J</code>
                                <p title="J">한다면 함</p>
                            </a>
                            <span>VS</span>
                            <a className="btn" title="P" onClick={m_Click}>
                                <p title="P">뭐부터 하지?</p>
                                <code title="P" className="right">P</code>
                            </a>
                        </div>
                    </div>
                    <div className="mbti-text">
                        <div className="result">
                            <span></span>
                            <button className="reInput" value="확인" onClick={reset}>초기화</button>
                        </div>
                        <button className="okInput" value="확인" onClick={okClick}>결과 보기</button>
                    </div>
                </div>
                <div className="allResult">
                    <article className="reTitle">
                        <h2>결과 보기</h2>
                        <p>
                            #MBTI #성격유형 #심리테스트 #성격테스트
                        </p>
                        <span>
                            당신의 성격 유형은
                        </span>
                    </article>
                    <div className="reTextAll">
                        <div className="reText">
                            <div className="reMbti">
                                <h3></h3>
                            </div>
                            <div className="reSpan">
                                <span></span>
                            </div>
                            <div className="reP">
                                <p></p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            
        </>
    )
}
