import DataWeather from "../data/WeatherAll.json";
import {useState} from "react";
import Logo from "../img/top_logo.gif";
import backImg from "../img/back1.png";
export default function Weather(){

/*      "so2Grade":"1", //아황산가스 농도
        "coValue":"0.5", //일산화탄소 농도
        "pm10Value":"22", //미세먼지(PM10) 농도
        "pm25Value":"11", //미세먼지(PM2.5) 농도
        "sidoName":"서울", //시도 명
        "dataTime":"2021-09-09 15:00", //측정일시
        "no2Value":"0.027", //이산화질소 농도
        "stationName":"중구", //측정소 명
        "o3Value":"0.050" //오존 농도 */

    let arr = []; 
    let [stat,setData] = useState(DataWeather.items);

    //title 나타내기
    for(let name in DataWeather.TITLE){
        arr.push(DataWeather.TITLE[name])
    }
    function statChange(e){
        if(e.target.value == "All"){
            setData(DataWeather.items)
            
        }else{
            setData(
                DataWeather.items.filter(station => (
                    station['sidoName'] == e.target.value
                ))
            );
        }
    }
    /* function filt(){
        for(let fName in DataWeather.items.sidoName){
            filtName.push()
        }
    } */
    let binArr = [];
    
    DataWeather.items.forEach(function(stat,k){
        let a = stat['sidoName'];
        
        if(!binArr.some((v)=>(v==a)) ){
            binArr.push(a);
        }
    })




    return(
        <>  
            <section className="sec">
                <article className="sec_top">
                    <h2>
                        <img src={Logo} alt=""/>
                    </h2>
                    <p>- 대기 오염 정보 -</p>
                    <div>
                        <select id="station" onChange={statChange}>
                            {
                                DataWeather.items.map(function(stat,k){
                                    if(binArr[k] != undefined){
                                        return (
                                            <option key={k}>
                                                {binArr[k]}
                                            </option>
                                        )
                                    } 
                                })
                            }
                        </select>
                        <button type="submit" value="All" onClick={statChange}>All</button>
                    </div>
                </article>
                <table border="1" className="tab">
                    <thead>
                        <tr>
                            {
                                arr.map(function(name,k){
                                    return <th key={k}>{name}</th>;
                                })
                            }
                        </tr>
                    </thead>
                    <tbody>
                        {
                            stat.map(function(dataView,k){
                                return(
                                    <tr key={k}>
                                        <td>{dataView['sidoName']}</td>
                                        <td>{dataView['stationName']}</td>
                                        <td>{dataView['pm10Value']}</td>
                                        <td>{dataView['pm25Value']}</td>
                                        <td>{dataView['so2Grade']}</td>
                                        <td>{dataView['coValue']}</td>
                                        <td>{dataView['no2Value']}</td>
                                        <td>{dataView['o3Value']}</td>
                                        <td>{dataView['dataTime']}</td>
                                    </tr>
                                );
                            })
                        }
                    </tbody>
                </table>
                <div className="back">
                    <img src={backImg} alt="back"/>
                </div>
            </section>


        </>
    )
}